<template>
    <div>
        <div class="details-header">
            <div class="details-back" @click="$router.go(-1)"></div>
            <div class="details-wrap">
                <div :class="{'wrap-name':true,active:isdetailsStyle}" @click="$router.push('/goods/details?gid='+gid)">商品</div>
                <div :class="{'wrap-name':true,active:iscontentStyle}"   @click="$router.push('/goods/details/content?gid='+gid)">详情</div>
                <div :class="{'wrap-name':true,active:isreviewStyle}" @click="$router.push('/goods/details/review?gid='+gid)">评价</div>
            </div>
            <div class="details-cart" id="details-cart" @click="$router.push('/cart?from='+'goods_details')">
                <div data-v-e03f2776="" class="spot" v-show="goodsDatas.length>0"></div>
            </div>
        </div>
        <div class="details-main">
           <router-view></router-view>
        </div>
    </div>
</template>

<script>
    import {mapState, mapMutations} from "vuex"
    export default {
        data(){
            return {
                gid:this.$route.query.gid?this.$route.query.gid:"",
                isdetailsStyle:false,
                iscontentStyle:false,
                isreviewStyle:false
            }
        },
        created(){
            console.log(this.$route.name)
            this.changeStyle(this.$route.name);
        },
        methods:{
            changeStyle(name){
                switch (name) {
                    case "goods-item":
                        this.isdetailsStyle = true;
                        this.iscontentStyle =false;
                        this.isreviewStyle = false;
                        break;
                    case "goods-content":
                        this.isdetailsStyle = false;
                        this.iscontentStyle =true;
                        this.isreviewStyle = false;
                        break;
                    case "goods-review":
                        this.isdetailsStyle = false;
                        this.iscontentStyle =false;
                        this.isreviewStyle = true;
                        break;
                    default:
                        this.isdetailsStyle = true;
                        this.iscontentStyle =false;
                        this.isreviewStyle = false;
                        break;


                }
            }
        },
        computed: {
            ...mapState({
                //购物车数据
                goodsDatas: state => state.cart.goodsDatas
            })
        },
        beforeRouteUpdate(to,from,next){
            this.changeStyle(to.name);
            next();
        }

    }
</script>

<style scoped>
.details-header {
    position: fixed;
    z-index: 11;
    left: 0;
    top: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 1rem;
    background: #fff;
    box-sizing: border-box;
    padding: 0 .3rem;
}
.details-header .details-back {
    width: .8rem;
    height: .8rem;
    background:url("../../assets/images/home/goods/back.png") no-repeat center;
    -webkit-background-size: 100%;
    background-size: 100%;
 }
.details-header .details-cart .spot {
    width: .2rem;
    height: .2rem;
    background-color: #cc0004;
    border-radius: 100%;
    position: absolute;
    z-index: 1;
    right: 0;
    top: .15rem;
}
.details-header .details-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 70%;
    height: 100%;

}

.details-header .details-wrap .wrap-name {
    width: auto;
    height: 96%;
    overflow: hidden;
    line-height: 1rem;
    text-align: center;
    margin-left: 9%;
    margin-right: 9%;
    font-size: .36rem;
}
.details-header .details-wrap .wrap-name.active {
    border-bottom: 2px solid #fda208;
}
.details-header .details-cart {
    position: relative;
    width: .8rem;
    height: .8rem;
    background:url("../../assets/images/home/goods/cart.png") no-repeat center;
    -webkit-background-size: 100%;
    background-size: 100%;
}
    .details-main {
        margin-top: 1.15rem;
    }
</style>